body {
  margin: 0;
  font-family: "Lato";
  text-decoration: none;
}

.cointenerMenu{
    background: #131313;
    width: 20%;
    text-align: center;
    padding: 20px;
    height: auto;
    box-shadow: 5px 0px black;
}
.logoMenu{
    margin-top:20px;
    width: 250px;
}
.cointenerButtons{
    margin-top: 40px;
    text-decoration: none;
}
.buttonMenu{
    color: white;
    margin-bottom:30px;
    font-family: "Lato";
    font-size: 18px;
}
.cointenerMain {
    background-color: black;
    background-image: url("data:image/svg+xml,%3Csvg width='16' height='20' viewBox='0 0 16 20' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='%231c1c1c' fill-opacity='1' fill-rule='evenodd'%3E%3Cpath d='M8 0v20L0 10M16 0v10L8 0M16 10v10H8'/%3E%3C/g%3E%3C/svg%3E");
    display: flex;
    height: 100vh;
}
.titleMain{
    color: cornsilk;
    padding: 40px;
    font-size: 20px;
    font-family: "Lato";
}
.cointenerAbout{
    background-color: #000000;
    background-image: url("data:image/svg+xml,%3Csvg width='16' height='20' viewBox='0 0 16 20' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='%231c1c1c' fill-opacity='1' fill-rule='evenodd'%3E%3Cpath d='M8 0v20L0 10M16 0v10L8 0M16 10v10H8'/%3E%3C/g%3E%3C/svg%3E");
    /*background: #131313;*/
    display: flex;
    height: 100vh;
}
.titleAbout{
    font-family: "Lato";
    font-size: 30px;
    padding: 60px;
    color: cornsilk;
}
.cointenerAllCards{
    display: inline-block;
  /* overflow-y: auto;  */
    width: 50%;
}
.cointenerCard{
    background: black;
    width: 400px;
    height: 90%;
    margin:  0 5px 0 5px;
}
.imgCard{
    width: 50%;
    margin: 0 auto;
    text-align: center;
}
.infoProyect{
    font-family: "Lato";
    font-size: 12px;
    padding: 15px;
}
.cointenerButtons{
  margin: 15px 0 20px 0;  
}
.buttonProyect{
    margin: 0 15px 0 0;
    padding: 5px 15px 5px 15px;
}
.cointenerWorks{
    background-color: #000000;
    background-image: url("data:image/svg+xml,%3Csvg width='16' height='20' viewBox='0 0 16 20' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='%231c1c1c' fill-opacity='1' fill-rule='evenodd'%3E%3Cpath d='M8 0v20L0 10M16 0v10L8 0M16 10v10H8'/%3E%3C/g%3E%3C/svg%3E");

    /*background: #131313;*/
    display: flex;
    height: 100vh;
}
.cointenerCardsProyects{
    width: 80%;
}
.titleWorks{
    text-align: center;
    margin: 0;
    /*width: 100%;*/
    font-family: "Lato";
    font-size: 40px;
    color: cornsilk;
    padding: 0px;
    margin: 50px 0 60px 0;
}
.cointenerContact{
    background: black;
    background-image: url("data:image/svg+xml,%3Csvg width='16' height='20' viewBox='0 0 16 20' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='%231c1c1c' fill-opacity='1' fill-rule='evenodd'%3E%3Cpath d='M8 0v20L0 10M16 0v10L8 0M16 10v10H8'/%3E%3C/g%3E%3C/svg%3E");
    display: flex;
    height: 100vh;
}
.titleContact{
    font-family: "Lato";
    font-size: 50px;
    padding:100px;
    color: cornsilk;
}
.spanContact{
    color: #00FA77;
}
